footer {
    display: -ms-flexbox;
    display: flex;

    width: 100%;
    height: 50px;

    background-color: var(--main-color);
}

footer ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    list-style-type: none;
    width: 80%;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    font-family: 'Oswald', sans-serif;
    letter-spacing: 1px;
}

footer a {
    color: white;
    justify-self: flex-start;
    text-decoration: none;
}


